import jobs from "@/app/data/jobs";

export default function jobDescription({ params }) {

    const jobDetails = jobs.find(job => job.title.toLocaleLowerCase().replace(/\s+/g, '-') === params.slug);

    return(
       <div className="flex flex-col items-start justify-center p-4 w-4/5 m-auto">
        <h1 className="font-bold text-5xl py-4 border-double border-slate-950">{jobDetails.title}

        </h1>

        <h2>{jobDetails.company}</h2>
        <h3>{jobDetails.location}</h3>
        <img src={`/${jobDetails.id !== null ? jobDetails.id : 'hire'}.png`} alt={jobDetails.title} />
       </div>
    )
}